import { ErrorHandler } from './ErrorHandler';
import { showErrorSrc176Evid102 } from '@wix/error-handler-core/@wix/bi-logger-os-data/v2';
import { ErrorAccessor } from '../state/ErrorAccessor';
import { withoutDefaults } from '../util/withoutDefaults';
import { ShowErrorActionProps } from '@wix/error-handler-core/types';

export interface ReportErrorBiParams {
  readonly message: string;
  readonly action?: ShowErrorActionProps;
  readonly errorAccessor: ErrorAccessor;
}

export function reportShowErrorBi(
  errorHandler: ErrorHandler,
  params: ReportErrorBiParams,
) {
  const { message, action, errorAccessor } = params;
  const { biLogger, translations } = errorHandler;

  biLogger.report(
    withoutDefaults(showErrorSrc176Evid102)({
      session_id: errorAccessor.errorHandlerState?.sessionId,
      displayed_message: message,
      displayed_action: action?.text,
      action_was_provided: String(action?.onClick != null),
      isEn: translations.i18n.language === 'en',
    }),
  );
}
